import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { changeField, initializeForm, register } from '../../modules/auth';
import AuthForm from '../../components/auth/AuthForm';
import { check } from '../../modules/user';
import { useNavigate } from 'react-router-dom';

const RegisterForm = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { form, auth, authError, user } = useSelector(({ auth, user }) => ({
    form: auth.register,
    auth: auth.auth,
    authError: auth.authError,
    user: user.user,
  }));
  useEffect(() => {
    dispatch(initializeForm('register'));

  }, [dispatch]);

  const [error, setError] = useState(null);

  const onChange = (e) => {
    const { value, name } = e.target;
    dispatch(changeField({ form: 'register', key: name, value }));
  };
  const onSubmit = (e) => {
    e.preventDefault();
    const { user_email, user_password, passwordConfirm, user_name } = form;

    //  
    if ([user_email, user_password, passwordConfirm].includes('')) {
      setError('   .');
      return;
    }
    const emailRegex =
      /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
    if (!emailRegex.test(user_email)) {
      setError('   .');
      dispatch(changeField({ form: 'register', key: 'user_email', value: '' }));

      return;
    }

    //   
    if (user_password !== passwordConfirm) {
      setError('  .');
      dispatch(changeField({ form: 'register', key: 'user_password', value: '' }));
      dispatch(
        changeField({ form: 'register', key: 'passwordConfirm', value: '' }),
      );
      return;
    }
    const pwRegex =
      /^.*(?=^.{8,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;

    if (!(pwRegex.test(user_password) || pwRegex.test(passwordConfirm))) {
      setError(' , ,   8~15 .');
      dispatch(changeField({ form: 'register', key: 'user_password', value: '' }));
      dispatch(
        changeField({ form: 'register', key: 'passwordConfirm', value: '' }),
      );
      return;
    }
    dispatch(register({ user_email, user_password, user_name }));
  };

  //   DB
  useEffect(() => {
    if (authError) {
      if(authError ==={}){
      return;
      }
      if (authError.response.status === 500) {
        setError(' ');
        return;
      }
      if (authError.response.status === 429) {
        setError('  .');
        return;
      }
      if (authError.response.status === 439) {
        setError('  .');
        return;
      }
      console.log(' ');
      console.log(authError);
      return;
    }
    else{
      setError('');
    }
    if (auth) {
      console.log(' ');
      console.log(auth);
      alert(' !   ');
      dispatch(initializeForm('auth'));
      navigate('/login');

      return;
    }
  }, [auth, authError, dispatch]);

  return (
    <AuthForm
      type="register"
      form={form}
      onChange={onChange}
      onSubmit={onSubmit}
      error={error}
    />
  );
};

export default RegisterForm;
