import { createAction, handleActions } from "redux-actions";
import createRequestSaga, { createRequestActionTypes } from "../lib/createRequestSaga";
import * as postsAPI from '../lib/api/post';
import { takeLatest } from 'redux-saga/effects';

const INITIALIZE = 'write/INITIALIZE'; //   
const INITWRITE = 'write/INITWRITE'; //  
const CHANGE_FIELD = 'write/CHANGE_FIELD'; //  key  
const [
  WRITE_POST,
  WRITE_POST_SUCCESS,
  WRITE_POST_FAILURE,
] = createRequestActionTypes('write/WRITE_POST'); //   
const [
  EDIT_POST,
  EDIT_POST_SUCCESS,
  EDIT_POST_FAILURE,
] = createRequestActionTypes('write/EDIT_POST'); //  
const [
  DELETE_POST,
  DELETE_POST_SUCCESS,
  DELETE_POST_FAILURE,
] = createRequestActionTypes('write/DELETE_POST'); //  

const [
  CATEGORYS, 
  CATEGORYS_SUCCESS, 
  CATEGORYS_FAILURE
] = createRequestActionTypes('write/CATEGORY');// 
const [
  MISSION, 
  MISSION_SUCCESS, 
  MISSION_FAILURE
] = createRequestActionTypes('write/MISSION');// 

const SET_ORIGINAL_POST = 'write/SET_ORIGINAL_POST';

export const initialize = createAction(INITIALIZE);
export const initWrite = createAction(INITWRITE);
export const changeField = createAction(CHANGE_FIELD, ({ key, value }) => ({
  key,
  value,
}));
export const writePost = createAction(WRITE_POST, ({ post_title, post_contents,post_category, post_mission, formdata, config }) => ({
  post_title,
  post_contents,
  post_category,
  post_mission, 
  formdata, 
  config
}));


export const editPost = createAction(EDIT_POST, ({ post_id, post_title, post_contents, post_category, post_mission }) => ({
  post_id,
  post_title,
  post_contents,
  post_category,
  post_mission
}));
export const deletePost = createAction(DELETE_POST, id => id);

export const categorys = createAction(CATEGORYS);
export const mission = createAction(MISSION);
export const setOriginalPost = createAction(SET_ORIGINAL_POST, post => post);


//  
const writePostSaga = createRequestSaga(WRITE_POST, postsAPI.writePost);
const editPostSaga = createRequestSaga(EDIT_POST, postsAPI.editPost);
const deletePostSaga = createRequestSaga(DELETE_POST, postsAPI.deletePost);
const categorysSaga = createRequestSaga(CATEGORYS, postsAPI.categorysPost);
const missionSaga = createRequestSaga(MISSION, postsAPI.mission);
export function* writeSaga() {
  yield takeLatest(CATEGORYS, categorysSaga);
  yield takeLatest(MISSION, missionSaga);
  yield takeLatest(WRITE_POST, writePostSaga);
  yield takeLatest(EDIT_POST, editPostSaga);
  yield takeLatest(DELETE_POST, deletePostSaga);
}




const initialState = {
  post_title: '',
  post_contents: '',
  post_category: 1,
  post_image:[],
  categorys:[],
  categorysError:null,
  mission:null,
  missionError:null,
  post: null,
  postError: null,
  originalPostId: null,
};

const write = handleActions(
  {
    [INITIALIZE]: state => initialState, // initialState    
    [INITWRITE]: state =>({
      ...state,
      post_title: '',
      post_contents: '',
      post_image:[],
      post_category: 1,
      categorysError:null,
      missionError:null,
      postError: null,
      originalPostId: null,
    }),

    [CHANGE_FIELD]: (state, { payload: { key, value } }) => ({
      ...state,
      [key]: value, //  key  
    }),
    [WRITE_POST]: state => ({
      ...state,
      // post postError 
      categorys:[],
      categorysError:null,
      mission:null,
      missionError:null,
      post: null,
      postError: null,
    }),
    //   
    [WRITE_POST_SUCCESS]: (state, { payload: post }) => ({
      ...state,
      post,
    }),
    //   
    [WRITE_POST_FAILURE]: (state, { payload: postError }) => ({
      ...state,
      postError,
    }),
    [EDIT_POST]: state => ({
      ...state,
      // post postError 
      categorys:[],
      categorysError:null,
      mission:null,
      missionError:null,
      post: null,
      postError: null,
    }),
    //   
    [EDIT_POST_SUCCESS]: (state, { payload: post }) => ({
      ...state,
      post,
    }),
    //   
    [EDIT_POST_FAILURE]: (state, { payload: postError }) => ({
      ...state,
      postError,
    }),
    //   
    [DELETE_POST_SUCCESS]: (state, { payload: post }) => ({
      ...state,
      post,
    }),
    //   
    [DELETE_POST_FAILURE]: (state, { payload: postError }) => ({
      ...state,
      postError,
    }),


    [CATEGORYS]: state => ({
      ...state,
      // post postError 
      categorys:[],
      categorysError:null,
      mission:null,
      missionError:null,
      post: null,
      postError: null,
    }),
    //   
    [CATEGORYS_SUCCESS]: (state, { payload: categorys }) => ({
      ...state,
      categorys:categorys.result.categorys,
    }),
    //   
    [CATEGORYS_FAILURE]: (state, { payload: categorysError }) => ({
      ...state,
      categorysError,
    }),
    
    [MISSION]: state => ({
      ...state,
      // post postError 
      categorys:[],
      categorysError:null,
      mission:null,
      missionError:null,
      post: null,
      postError: null,
    }),
 
    [MISSION_SUCCESS]: (state, { payload: mission }) => ({
      ...state,
      mission:mission.result.mission,
    }),
   
    [MISSION_FAILURE]: (state, { payload: missionError }) => ({
      ...state,
      missionError,
    }),
    [SET_ORIGINAL_POST]:(state, {payload:post})=>({
      ...state,
      post_title: post.post_title,
      post_contents: post.post_contents,
      post_category: post.post_category,
      post_mission: post.post_mission,
      originalPostId: post.post_id,
    })


  },
  initialState,
)

export default write;